export enum MediaElementEvents {
  Abort = 'abort',
  CanPlay = 'canplay',
  CanPlayThrough = 'canplaythrough',
  DurationChange = 'durationchange',
  Emptied = 'emptied',
  Ended = 'ended',
  Error = 'error',
  LoadStart = 'loadstart',
  LoadedData = 'loadeddata',
  LoadedMetadata = 'loadedmetadata',
  Pause = 'pause',
  Play = 'play',
  Playing = 'playing',
  Progress = 'progress',
  Ratechange = 'ratechange',
  Resize = 'resize',
  Stalled = 'stalled',
  Suspend = 'suspend',
  Seeked = 'seeked',
  Seeking = 'seeking',
  TimeUpdate = 'timeupdate',
  VolumeChange = 'volumechange',
  Waiting = 'waiting'
}

export class STDPlayer {

  private mediaElement: HTMLMediaElement;
  private source: HTMLSourceElement;

  private desiredLiveOffset: number = 2;
  private liveOffsetThreshold: number = 10;

  constructor(mediaElement: HTMLMediaElement, source: HTMLSourceElement) {
    console.log(`## [STDPlayer] constructor | player created`);
    this.source = source;
    this.mediaElement = mediaElement;
    this.mediaElement.setAttribute('playsinline', '');
    this.mediaElement.setAttribute('controls', '');
    this.mediaElement.preload = 'metadata';

    this.mediaElement.addEventListener(MediaElementEvents.Error, (e) => {
      console.error(e)

    });
    this.mediaElement.addEventListener(MediaElementEvents.DurationChange, (e) => {
      this.actionAt(MediaElementEvents.DurationChange);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Ended, (e) => {
      this.actionAt(MediaElementEvents.Ended);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Error, (e) => {
      this.actionAt(MediaElementEvents.Error);

    });
    this.mediaElement.addEventListener(MediaElementEvents.LoadStart, (e) => {
      this.actionAt(MediaElementEvents.LoadStart);

    });
    this.mediaElement.addEventListener(MediaElementEvents.LoadedData, (e) => {
      this.actionAt(MediaElementEvents.LoadedData)
      // this.mediaElement.play();
      if(this.liveEdgeOffset < this.liveOffsetThreshold){
        this.gotoLiveEdge();
      }

    });
    this.mediaElement.addEventListener(MediaElementEvents.LoadedMetadata, (e) => {
      this.actionAt(MediaElementEvents.LoadedMetadata);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Pause, (e) => {
      this.actionAt(MediaElementEvents.Pause);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Playing, (e) => {
      this.actionAt(MediaElementEvents.Playing);
      if (this.liveEdgeOffset > this.liveOffsetThreshold) {
        this.gotoLiveEdge()
      }

    });
    this.mediaElement.addEventListener(MediaElementEvents.Seeked, (e) => {
      this.actionAt(MediaElementEvents.Seeked);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Seeking, (e) => {
      this.actionAt(MediaElementEvents.Seeking);

    });
    this.mediaElement.addEventListener(MediaElementEvents.TimeUpdate, (e) => {
      // if (this.liveEdgeOffset > this.liveOffsetThreshold) {
      //   this.gotoLiveEdge()
      // }

      this.timeupdate();
    });
    this.mediaElement.addEventListener(MediaElementEvents.VolumeChange, (e) => {
      this.actionAt(MediaElementEvents.VolumeChange);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Waiting, (e) => {
      this.actionAt(MediaElementEvents.Waiting);

    });
    this.mediaElement.addEventListener(MediaElementEvents.Stalled, (e) => {
      this.actionAt(MediaElementEvents.Stalled);

    });
    this.mediaElement.addEventListener(MediaElementEvents.CanPlay, (e) => {
      this.actionAt(MediaElementEvents.CanPlay);
      try {
        if (this.mediaElement.buffered.end(this.mediaElement.buffered.length - 1) < this.liveOffsetThreshold) {
          console.log(`## [STDPlayer] CanPlay | We are short -> `,this.mediaElement.buffered.end(this.mediaElement.buffered.length - 1))
          this.mediaElement.currentTime = 100000000
        }else{
          console.log(`## [STDPlayer] CanPlay`, this.mediaElement.currentTime, this.mediaElement.buffered.end(this.mediaElement.buffered.length - 1));
        }
      } catch (e) {
        console.log(`## [STDPlayer] CanPlay | Buffer not ready...`);
      }


    });

    this.mediaElement.addEventListener(MediaElementEvents.Ratechange, (e) => {
      this.actionAt(MediaElementEvents.Ratechange);

    });
  }

  private gotoLiveEdge() {
    if (this.mediaElement && this.bufferedEnd) {
      console.log(`## [STDPlayer] gotoLiveEdge | GoToLive! `);
      this.mediaElement.currentTime = this.bufferedEnd - this.desiredLiveOffset;
    }
  }


  public get duration(): number {
    if (this.mediaElement.duration === Infinity) {
      if (this.mediaElement.buffered.length >= 1) {
        return this.mediaElement.buffered.end(0) - this.mediaElement.buffered.start(0);
      } else {
        return -1;
      }
    }
    return this.mediaElement.duration ?? -1
  }

  public get currentTime(): number {
    return this.mediaElement.currentTime ?? 0;
  }

  public get liveEdgeOffset(): number {

    if (this.bufferedEnd) {
      return this.bufferedEnd - this.mediaElement.currentTime;
    } else {
      return -1;
    }
  }
  public get seekableLiveEdgeOffset(): number {

    if (this.seekableRangeEnd) {
      return this.seekableRangeEnd - this.mediaElement.currentTime;
    } else {
      return -1;
    }
  }

  public get bufferedStart(): number {
    if (this.mediaElement.buffered.length >= 1) {
      return this.mediaElement.buffered.start(0) ?? 0;
    }
    return 0;
  }

  public get bufferedEnd(): number {
    if (this.mediaElement.buffered.length >= 1) {
      return this.mediaElement.buffered.end(this.mediaElement.buffered.length - 1) ?? 0;
    }
    return 0;
  }
  public get seekableRangeStart(): number {
    if (this.mediaElement.seekable.length >= 1) {
      return this.mediaElement.seekable.start(0) ?? 0;
    }
    return 0;
  }

  public get seekableRangeEnd(): number {
    if (this.mediaElement.seekable.length >= 1) {
      return this.mediaElement.seekable.end(this.mediaElement.seekable.length - 1) ?? 0;
    }
    return 0;
  }



  public timeupdate() {

  }

  private actionAt(at: string) {
    // try{
    //   console.log(`## [STDPlayer] actionAt | ${at} buffered:`, this.bufferedEnd , this.bufferedEnd - this.mediaElement.currentTime);
    //   // console.log(`## [STDPlayer] actionAt | ${at} seekable:`, this.seekableRangeEnd, this.seekableRangeEnd -this.mediaElement.currentTime);
    //
    // }catch (e){
    //   console.log(`## [STDPlayer] actionAt | seekable not ready...`);
    //   console.log(`## [STDPlayer] actionAt | ${at}`, this.mediaElement.currentTime);
    // }


  }

  public load(src: string) {
    this.source.src = src;
    this.mediaElement.load();
  }

  public reset() {

  }

}
