import * as React from 'react';
import { ReactNode, useEffect, useState } from "react";
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import PlayerView from "./PlayerView";

interface PlayerContainerProps {
  children?: ReactNode | ReactNode[]
};

export default function PlayerContainer(props: PlayerContainerProps) {
  const [open, setOpen] = React.useState(false);
  const [source, setSource] = useState<string | undefined>(undefined);
  const toggleDrawer = (newOpen: boolean) => () => {
    setOpen(newOpen);
  };

  const DrawerList = (
    <Box sx={{width: 250}} role="presentation" onClick={toggleDrawer(false)}>
      <Button onClick={event => setSource('https://kanal75xto-llhls.akamaized.net/live/Data/atg-kanal-15-02/HLS-LL-Ateme-1sec/atg-kanal-15-02.m3u8')}>Load</Button>
    </Box>
  );

  return (
    <div style={{
      paddingTop:'20px'
    }}>
      <PlayerView source={source}></PlayerView>
    </div>
  );
}


