import * as React from 'react';
import { ReactNode, useEffect, useState } from "react";
import Box from '@mui/material/Box';
import Slider from '@mui/material/Slider';
import { IconButton, Stack } from "@mui/material";
interface PlayerControlsProps {
    children?: ReactNode | ReactNode[]
    bufferedStart:number;
    bufferedEnd:number;
    seekableRangeStart:number;
    seekableRangeEnd:number;
    currentTime:number;
};


export default function PlayerControls(props:PlayerControlsProps){
  const [bufferMarks, setBufferMarks] = useState<Array<{value:number, label:string}>>([

  ])
  const [seekableMarks, setSeekableMarks] = useState<Array<{value:number, label:string}>>([

  ])
  useEffect(() => {
    setBufferMarks([
      {
        value:props.bufferedStart,
        label:`${props.bufferedStart.toFixed(0)}`
      },
      {
        value:props.bufferedEnd,
        label:`${props.bufferedEnd.toFixed(0)}`
      },
      {
        value:props.currentTime,
        label:`${props.currentTime.toFixed(0)}`
      },

      ]
    )
    setSeekableMarks([
        {
          value:props.seekableRangeStart,
          label:`${props.seekableRangeStart.toFixed(0)}`
        },
        {
          value:props.seekableRangeEnd,
          label:`${props.seekableRangeEnd.toFixed(0)}`
        },
        {
          value:props.currentTime,
          label:`${props.currentTime.toFixed(0)}`
        },

      ]
    )

    console.log(`## [PlayerControls]  | buffer:  `,props.bufferedStart,props.bufferedEnd);
    console.log(`## [PlayerControls]  | seekable:`,props.seekableRangeStart,props.seekableRangeEnd);
  },[
    props.bufferedStart,
    props.bufferedEnd,
    props.currentTime
  ])

  function valuetext(value: number) {
    return `${value}`;
  }
  return (
      <div>
          <Stack direction={"column"}>
            <Box sx={{paddingLeft: '2%', width: '100%'}}>
              <p>Buffer</p>
              <Slider
                sx={{width: '80%'}}
                max={props.bufferedEnd}
                min={props.bufferedStart}
                value={props.currentTime}
                aria-label="Custom marks"
                defaultValue={20}
                getAriaValueText={valuetext}
                color={'success'}
                step={10}
                valueLabelDisplay="auto"
                marks={bufferMarks}
              />
              <p>Seekable</p>
              <Slider
                sx={{width: '80%'}}
                max={props.seekableRangeEnd}
                min={props.seekableRangeStart}
                value={props.currentTime}
                aria-label="Custom marks"
                defaultValue={20}
                getAriaValueText={valuetext}
                step={10}
                valueLabelDisplay="auto"
                marks={seekableMarks}
              />
            </Box>
          </Stack>
      </div>
  );
}

