import * as React from 'react';
import { createRef, ReactNode, useEffect, useState } from "react";
import { STDPlayer } from "../STDPlayer";
import PlayerControls from "./PlayerControls";

interface PlayerViewProps {
  children?: ReactNode | ReactNode[]
  source?:string
};

export default function PlayerView(props: PlayerViewProps) {
  const [player, setPlayer] = useState<STDPlayer | undefined>(undefined);
  const [updator, setUpdator] = useState<number>(0)
  const [duration, setDuration] = useState<number>(0)
  const [liveEdgeOffset, setLiveEdgeOffset] = useState<number>(0)
  const [currentTime, setCurrentTime] = useState<number>(0)
  const [bufferedStart, setBufferedStart] = useState<number>(0)
  const [bufferedEnd, setBufferedEnd] = useState<number>(0)
  const [seekableRangeStart, setSeekableRangeStart] = useState<number>(0)
  const [seekableRangeEnd, setSeekableRangeEnd] = useState<number>(0)
  const [seekableLiveEdgeOffset, setSeekableLiveEdgeOffset] = useState<number>(0)
  useEffect(() => {
    const mediaElement = document.getElementById('mediaElement');
    const source = document.getElementById('source');
    if (!player && mediaElement && source) {
      const player = new STDPlayer(mediaElement as HTMLMediaElement, source as HTMLSourceElement);
      player.timeupdate = ()=>{
        setDuration(player.duration);
        setLiveEdgeOffset(player.liveEdgeOffset);
        setSeekableLiveEdgeOffset(player.seekableLiveEdgeOffset);
        setCurrentTime(player.currentTime);
        setBufferedEnd(player.bufferedEnd);
        setBufferedStart(player.bufferedStart);
        setSeekableRangeStart(player.seekableRangeStart);
        setSeekableRangeEnd(player.seekableRangeEnd);
      }

      setPlayer(player);

      player.load('https://kanal75xto-llhls.akamaized.net/live/Data/atg-kanal-15-02/HLS-LL-Ateme-1sec/atg-kanal-15-02.m3u8')
    }else{
      setTimeout(()=>{
        setUpdator(updator+1);
      },500)
    }
  }, [updator])

  useEffect(() => {
    if(player && props.source){
      player.load(props.source);
    }
  }, [props.source]);

  return (
    <div style={{
      width: '100%',
      height: '50vh',
    }}>
      <video id={'mediaElement'} width={'100%'} height={'100%'}>
        <source id={'source'} src="" type="application/vnd.apple.mpegurl"/>
      </video>
      <PlayerControls bufferedStart={bufferedStart} bufferedEnd={bufferedEnd} currentTime={currentTime}
                      seekableRangeStart={seekableRangeStart} seekableRangeEnd={seekableRangeEnd}></PlayerControls>
      <p>{`CurrentTime: ${currentTime.toFixed(2)}`}</p>
      <p>{`Duration: ${duration.toFixed(2)}`}</p>
      <p>{`LiveEdgeOffset: ${liveEdgeOffset.toFixed(2)}`}</p>
      <p>{`seekableLiveEdgeOffset: ${seekableLiveEdgeOffset.toFixed(2)}`}</p>
    </div>
  );
}
